import { Component, OnInit } from '@angular/core';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Line } from 'src/models/Line';
import { Utils } from 'src/commons/Utils';
import { Factory } from 'src/models/Factory';
import { Section } from 'src/models/Section';
import { ToastText, ErrorMessage, MenuText, ErrorType } from 'src/commons/Constants';
import { ActionSheetController, ModalController, PopoverController } from '@ionic/angular';
import { LinhaMenuPage } from './linha-menu/linha-menu.page';
import { NewPageL } from './new/new.page';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-linha',
  templateUrl: './linha.page.html',
  styleUrls: ['./linha.page.scss'],
})
export class LinhaPage implements OnInit {

  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "LinhaPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  private lines: Array<Line> = new Array<Line>();
  public lineList: Array<Line> = new Array<Line>();
  private searchTerm: string;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    private api: BackendApiProvider,
    private utils: Utils,
    public actionSheetController: ActionSheetController,
    public modalController: ModalController,
    public popoverController: PopoverController,
    private pageDataService: PageDataService,
    private router: Router
  ) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.pageDataService.listUserEvent.subscribe(() => {
      let data = this.pageDataService.getData('newLine');
      if (!Utils.isVarInvalid(data)) {
        this.lines.splice(this.lines.indexOf(this.lines.filter(n => n.getId() == data.getId())[0]), 1);
        this.lines.push(data);
        this.filter(this.searchTerm)
      }
    })
    this.getLines()
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  ngOnInit() {
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getLines() {
    this.api.getLines().then((lines: Array<Line>) => {
      this.lines = lines;
      this.lineList = this.lines;
    }).catch((error) => {
      this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
    });
  }

  //-------------------------------------------------------------------------------------------------------------------------------//
  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(evt)) {
      this.lineList = this.lines;
      return;
    }
    this.searchTerm = evt;
    if (!this.searchTerm) {
      this.lineList = this.lines;
      return;
    }
    this.lineList = this.lines.filter(n => n.getNome().toLowerCase().includes(this.searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(line: Line) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [
        {
          text: MenuText.EDIT,
          handler: () => {
            this.editLine(line);
          }
        }, {
          text: MenuText.DELETE,
          role: 'destructive',
          handler: () => {
            this.api.removeLine(line.getId()).then(() => {
              this.utils.simpleToast(ToastText.REMOVE_SUCCESSFUL);
              this.lines.splice(this.lines.indexOf(line), 1);
              this.filter(this.searchTerm);
            }).catch((error) => {
              let message = JSON.parse(error._body).message as string;
              if (message.includes(ErrorType.OBJECT_IN_USE)) {
                this.utils.simpleToastError(ToastText.OBJECT_IN_USE);
              }
              else {
                this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG);
              }
            });
          }
        },
        {
          text: MenuText.CANCEL, role: 'cancel',
          handler: () => {
          }
        }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async editLine(line: Line = undefined) {
    const modal = await this.modalController.create({
      component: NewPageL,
      animated: true,
      showBackdrop: true,
      cssClass: 'popover_class',
      componentProps: {
        line: line
      }
    });
    return await modal.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async openMenu(ev: any) {
    const popover = await this.popoverController.create({
      component: LinhaMenuPage,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class'
    });
    return await popover.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  navigateDashboard() {
    this.router.navigate(['admin', 'dashboard'])
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}
